import React, { useEffect } from "react"
import * as THREE from "three"
import styled from "styled-components"
import gsap from "gsap"

const Section = styled.div`
  height: 100vh;
  color: #fff;
  font-size: 90px;
  font-weight: bold;
  display: flex;
  align-items: center;
  justify-content: center;
  h1 {
    z-index: 2;
  }
`
const Canvas = styled.canvas`
  position: fixed;
`

const Home = () => {
  const init = () => {
    const objectsDistance = 4
    const materialColor = "#FFFFFF"
    // Canvas
    const canvas = document.getElementById("canvas") ?? undefined
    // Scene
    const scene = new THREE.Scene()

    /**
     * Textures
     */
    const textureLoader = new THREE.TextureLoader()
    const ambientOcclusionTexture = textureLoader.load(
      "../textures/Stylized_Fur_002_ambientOcclusion.jpg"
    )
    const heightTexture = textureLoader.load(
      "../textures/Stylized_Fur_002_height.png"
    )
    const normalTexture = textureLoader.load(
      "../textures/Stylized_Fur_002_normal.jpg"
    )
    const colorTexture = textureLoader.load(
      "../textures/Stylized_Fur_002_basecolor.jpg"
    )
    const roughnessTexture = textureLoader.load(
      "../textures/Stylized_Fur_002_roughness.jpg"
    )

    colorTexture.repeat.set(2, 2)
    ambientOcclusionTexture.repeat.set(2, 2)
    normalTexture.repeat.set(2, 2)
    roughnessTexture.repeat.set(2, 2)
    colorTexture.wrapS = THREE.RepeatWrapping
    ambientOcclusionTexture.wrapS = THREE.RepeatWrapping
    normalTexture.wrapS = THREE.RepeatWrapping
    roughnessTexture.wrapS = THREE.RepeatWrapping
    colorTexture.wrapT = THREE.RepeatWrapping
    ambientOcclusionTexture.wrapT = THREE.RepeatWrapping
    normalTexture.wrapT = THREE.RepeatWrapping
    roughnessTexture.wrapT = THREE.RepeatWrapping

    const material = new THREE.MeshPhongMaterial({
      map: colorTexture,
      aoMap: ambientOcclusionTexture,
      normalMap: normalTexture,
      normalScale: new THREE.Vector2(5, 5),
      specularMap: heightTexture,
    })

    /**
     * Objects
     */
    // Meshes
    const mesh1 = new THREE.Mesh(
      new THREE.TorusGeometry(1, 0.4, 16, 60),
      material
    )
    const mesh2 = new THREE.Mesh(new THREE.SphereGeometry(1, 45, 45), material)
    const mesh3 = new THREE.Mesh(
      new THREE.TorusKnotGeometry(0.8, 0.35, 100, 16),
      material
    )
    // position
    mesh1.position.y = -objectsDistance * 0
    mesh2.position.y = -objectsDistance * 1
    mesh3.position.y = -objectsDistance * 2
    mesh1.position.x = 2
    mesh2.position.x = -2
    mesh3.position.x = 2
    const sectionMeshes = [mesh1, mesh2, mesh3]
    scene.add(mesh1, mesh2, mesh3)

    /**
     * Particles
     */
    // Geometry
    const particlesCount = 200
    const positions = new Float32Array(particlesCount * 3)
    for (let i = 0; i < particlesCount; i++) {
      positions[i * 3 + 0] = (Math.random() - 0.5) * 10
      positions[i * 3 + 1] =
        objectsDistance * 0.5 -
        Math.random() * objectsDistance * sectionMeshes.length
      positions[i * 3 + 2] = (Math.random() - 0.5) * 10
    }
    const particlesGeometry = new THREE.BufferGeometry()
    particlesGeometry.setAttribute(
      "position",
      new THREE.BufferAttribute(positions, 3)
    )
    // Material
    const particlesMaterial = new THREE.PointsMaterial({
      color: materialColor,
      sizeAttenuation: true,
      size: 0.03,
    })
    // Points
    const particles = new THREE.Points(particlesGeometry, particlesMaterial)
    scene.add(particles)

    /**
     * Lights
     */
    const directionalLight = new THREE.DirectionalLight("#ffffff", 2)
    directionalLight.position.set(0, 1, 0)
    scene.add(directionalLight)

    /**
     * Sizes
     */
    const sizes = {
      width: window.innerWidth,
      height: window.innerHeight,
    }

    window.addEventListener("resize", () => {
      // Update sizes
      sizes.width = window.innerWidth
      sizes.height = window.innerHeight

      // Update camera
      camera.aspect = sizes.width / sizes.height
      camera.updateProjectionMatrix()

      // Update renderer
      renderer.setSize(sizes.width, sizes.height)
      renderer.setPixelRatio(Math.min(window.devicePixelRatio, 2))
    })
    /**
     * Camera
     */
    const cameraGroup = new THREE.Group()
    scene.add(cameraGroup)

    // Base camera
    const camera = new THREE.PerspectiveCamera(
      35,
      sizes.width / sizes.height,
      0.1,
      100
    )
    camera.position.z = 6
    cameraGroup.add(camera)

    /**
     * Scroll
     */
    let scrollY = window.scrollY
    let currentSection = 0
    window.addEventListener("scroll", () => {
      scrollY = window.scrollY
      const newSection = Math.round(scrollY / sizes.height)
      if (newSection != currentSection) {
        currentSection = newSection
        gsap.to(sectionMeshes[currentSection].rotation, {
          duration: 1.5,
          ease: "power2.inOut",
          x: "+=6",
          y: "+=3",
          z: "+=1.5",
        })
      }
    })

    /**
     * Cursor
     */
    const cursor = {
      x: 0,
      y: 0,
    }
    window.addEventListener("mousemove", event => {
      cursor.x = event.clientX / sizes.width - 0.5
      cursor.y = event.clientY / sizes.height - 0.5
    })

    /**
     * Renderer
     */
    const renderer = new THREE.WebGLRenderer({
      canvas: canvas,
      alpha: true,
    })
    renderer.setSize(sizes.width, sizes.height)
    renderer.setPixelRatio(Math.min(window.devicePixelRatio, 2))

    /**
     * Animate
     */

    const clock = new THREE.Clock()
    let previousTime = 0
    const tick = () => {
      const elapsedTime = clock.getElapsedTime()
      const deltaTime = elapsedTime - previousTime
      previousTime = elapsedTime

      // Animate meshes
      for (const mesh of sectionMeshes) {
        mesh.rotation.x += deltaTime * 0.1
        mesh.rotation.y += deltaTime * 0.12
      }

      // Animate camera
      camera.position.y = -((scrollY / sizes.height) * objectsDistance)

      const parallaxX = cursor.x * 0.5
      const parallaxY = -cursor.y * 0.5
      cameraGroup.position.x +=
        (parallaxX - cameraGroup.position.x) * 5 * deltaTime
      cameraGroup.position.y +=
        (parallaxY - cameraGroup.position.y) * 5 * deltaTime

      // Render
      renderer.render(scene, camera)

      // Call tick again on the next frame
      window.requestAnimationFrame(tick)
    }

    tick()
  }
  useEffect(() => {
    init()
  }, [])
  return (
    <>
      <Canvas id="canvas"></Canvas>
      <Section>
        <h1>Home</h1>
      </Section>
      <Section>
        <h1>My Projects</h1>
      </Section>
      <Section>
        <h1>Contact me</h1>
      </Section>
    </>
  )
}

export default Home
